var clubBasePopup = (r) => {
  var paidDiff = r.paid_until - +new Date() / 1000;
  var diffUnit =
    paidDiff < 60
      ? "second"
      : paidDiff < 60 * 60
      ? "minute"
      : paidDiff < 24 * 60 * 60
      ? "hour"
      : "day";
  var unitDivider =
    diffUnit === "day"
      ? 24 * 60 * 60
      : diffUnit === "hour"
      ? 60 * 60
      : diffUnit === "minute"
      ? 60
      : 1;

  return (
    <>
      <div>
        <strong>Club base</strong>
      </div>
      <div>
        <strong>
          {r.club ? <>Owned by {entityLink("club", r.club)}</> : <>For sale</>}
        </strong>
      </div>
      {r.paid_until ? (
        <div>
          Lease expiry:
          {relativeTimeFormatter.format(
            Math.floor(paidDiff / unitDivider),
            diffUnit
          )}
        </div>
      ) : null}
      <div>Upfront cost: ${numberFormatter.format(r.base_price)}</div>
      <div>Renewal: ${numberFormatter.format(r.base_price / 10)} per month</div>
    </>
  );
};

